import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout/Layout";
// import Img from 'gatsby-image';
import { Grid } from "@material-ui/core"
import PageComponent from '../components/paging/PageComponent'
import { Link } from "gatsby"

export default function BlurbListTemplate({ data }) {
	const blurbs = data.allMarkdownRemark.edges
	
	// console.log(data);
	return (
		<Layout>
			<PageComponent
				path="/blurbs"
				currentPage={data.allMarkdownRemark.pageInfo.currentPage}
				totalPages={data.allMarkdownRemark.pageInfo.pageCount}
				hasPreviousPage={data.allMarkdownRemark.pageInfo.hasPreviousPage}
				hasNextPage={data.allMarkdownRemark.pageInfo.hasNextPage}
			/>	
			<div style={{ marginBottom: 40 }}>
				<h2 style={{color: 'white'}}>{data.allMarkdownRemark.pageInfo.totalCount} Blurbs</h2>
				<Grid container spacing={2}>
					<Grid item xs={8}>
						{
							blurbs.map(post => (
								<div key={"blurb" + post.node.fields.slug} style={{marginBottom: 10}}>
									<Link to={post.node.fields.slug} style={{ textDecoration: 'none' }}>
										<div className="box hover" style={{ width: '100%', padding: 15 }}>
											<div style={{ fontSize: '9pt', color: 'lightgrey' }}>{post.node.frontmatter.date}</div>
											<div className="titleText secondary">{post.node.frontmatter.title}</div>
											<div className="summaryText">{post.node.frontmatter.summary}</div>
										</div>
									</Link>
								</div>
							))
						}
					</Grid>
					<Grid item xs={4}>
					
					</Grid>
				</Grid>
			</div>
			<PageComponent
				path="/blurbs"
				currentPage={data.allMarkdownRemark.pageInfo.currentPage}
				totalPages={data.allMarkdownRemark.pageInfo.pageCount}
				hasPreviousPage={data.allMarkdownRemark.pageInfo.hasPreviousPage}
				hasNextPage={data.allMarkdownRemark.pageInfo.hasNextPage}
			/>	
		</Layout>
	)
}

export const query = graphql`
  query blogListQuery($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      pageInfo {
        currentPage
        hasNextPage
        hasPreviousPage
        itemCount
        pageCount
        totalCount
        perPage
      }
      edges {
        node {
            id
            fields {
                slug
            }
			frontmatter {
				date
				title
				summary
				key
			}
            excerpt
        }
      }
    }
  }
`