import { ArrowBack, ArrowForward } from "@material-ui/icons";
import React from "react";
import { PageButton } from "./PageButton";

interface Props {
    path: string;
    currentPage: number;
    totalPages: number;
    hasPreviousPage: boolean;
    hasNextPage: boolean;
}

const PageComponent = (props: Props) => {
    let previousPagePath = props.path;
    let nextPagePath = `${props.path}/${props.currentPage + 1}`;
    
    if(props.currentPage > 2) {
        previousPagePath = `${props.path}/${props.currentPage - 1}`;
    }

	return (
        <div style={{ display: 'flex', marginBottom: 10}}>
            {					
                props.hasPreviousPage &&
                <PageButton path={previousPagePath}>
                    <ArrowBack style={{marginTop: 'auto', marginBottom: 'auto'}} />
                </PageButton>
            }
            <div style={{marginBottom: 'auto', color: 'white'}}>
                Page {props.currentPage} of {props.totalPages}
            </div>
            {					
                props.hasNextPage &&
                <PageButton path={nextPagePath}>
                    <ArrowForward style={{marginTop: 'auto', marginBottom: 'auto'}} />
                </PageButton>
            }
        </div>
	)
}

export default PageComponent;
